import { Component, ViewChild } from '@angular/core';
import { IonicPage, Content, NavController, NavParams, LoadingController } from 'ionic-angular';
import { DatabaseProvider } from '../../providers/database/database';

/**
 * Generated class for the PicPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-pic',
  templateUrl: 'pic.html',
})
export class PicPage {
	@ViewChild(Content) content: Content;
	public cat: any;
	loaded: boolean = false;
	current: any;
	tabTop: any;

	constructor(public navCtrl: NavController, public navParams: NavParams, private database: DatabaseProvider, public loadingCtrl: LoadingController) {

		this.database.load()
			.then(data => {
				this.cat = data[parseInt(this.navParams.data.pageId, 10)];
				this.current = "seg0";

				let groupBy = 3;
				for (let i = 0; i < this.cat.sscats.length; i++) {
					this.cat.sscats[i].groups = [];
					for (let j = 0; j < this.cat.sscats[i].pics.length; j = j + groupBy) {
						let max = j + groupBy;
						if (max > this.cat.sscats[i].pics.length) {
							max = this.cat.sscats[i].pics.length;
							//console.log('max ' + max);
						}
						this.cat.sscats[i].groups.push(this.cat.sscats[i].pics.slice(j, max));
						//console.log(this.cat.sscats[i].pics.slice(j, max));
					}
					//console.log(this.cat.sscats[i].groups);
				}
				this.loaded = true;
				//this.setAnchor();
			});


		//this.cat = (this.picData.getCats())[parseInt(this.navParams.data.pageId, 10)];


	}

	/*setAnchor() {
		this.tabTop = [];
			for (let i = 0; i < this.cat.sscats.length; i++) {
				if (document.getElementById('sscat' + i)) {
					this.tabTop[i] = document.getElementById('sscat' + i).offsetTop;
				}
			}
	}*/

	/*ionViewDidLoad() {
		if (this.loaded){
			this.setAnchor();
		}


		/*this.content.ionScrollEnd.subscribe(($event: any) => {
			//console.log('ionScroll:');
			//console.log($event);
			let tmpSeg = 'seg0';
			for (let i = 1; i < this.tabTop.length; i++) {
				//console.log($event.scrollTop + ' ' + this.tabTop[i]);
				console.log($event);
				if ($event.scrollTop > this.tabTop[i]) {
					tmpSeg = 'seg' + i;
				} else {
					break;
				}
			}
			this.current = tmpSeg;
		});*/
	/*}*/

	scrollTo(element: string) {
		if (document.getElementById(element)) {

			let yOffset = document.getElementById(element).offsetTop;
			//console.log(yOffset);
			this.content.scrollTo(0, yOffset, 350);
		}
	}


}



